.t-container{
    width: 100%;
}
.t-header {
    display: -webkit-flex;
    display: flex;
    align-items: center;
    justify-content: flex-start;

    width: 100%;
    padding: 10px 10px;

    border:none;
    border-bottom: 1px solid rgba(18, 17, 39, 0.10);

    background: white;
}

.t-header:hover, .t-item:hover { 
    background: #F5F3FF;
}

.t-angle { 
    color: #8B5CF6;
    margin-right: 10px;
}

.t-item {
    display: -webkit-flex;
    display: flex;
    align-items: center;

    padding: 0px 0px;
    margin-left: 30px;

    border-bottom: 1px solid rgba(18, 17, 39, 0.10);
}

.t-item-content {
    display: -webkit-flex;
    display: flex;
    align-items: center;
    padding: 10px;

}

.t-item-name {
    margin-left: 10px;
    
    font-family: Poppins;
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    color: #283252;
}

.t-price {
    display: -webkit-flex;
    display: flex;
    align-items: center;
}

.t-field {

    height: 35px;
    width: 100px;
    padding-left: 13px;
    margin-left: 20px;

    border-radius: 8px;
    border: none;

    background: #F6F6F6;
 
}
